import React from 'react';
import s from './DayOpenDoors.module.css'
import preview from './DayOpenDoorsAccets/preview.svg'


let DayOpenDoors = () => {
    return (
        <div className={s.body}>
            <div className={s.dayOpenDoorsContent}>
                <h2>
                    <strong>
                        ДЕНЬ ОТКРЫТЫХ ДВЕРЕЙ
                    </strong>
                </h2>
                <iframe  className={s.preview} width="1226" height="520" src="https://www.youtube.com/embed/-oHgHG5CHNU"
                        title="YouTube video player" frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen></iframe>
            </div>

        </div>
    )
}

export default DayOpenDoors;