import React from 'react';
import s from './Slider.module.css'
import image1 from './SimpleSliderAccets/image1.png';
import image2 from './SimpleSliderAccets/image2.png';
import image3 from './SimpleSliderAccets/image3.png';
import image4 from './SimpleSliderAccets/image4.png';
import Slider from "react-slick";

function SampleNextArrow(props) {
    const { className, style, onClick} = props;
    return (
        <div
            className={className}
            style={{ ...style,  color: '#FFD43C', marginRight: '40px', paddingBottom: '100px'}}
            onClick={onClick}

        />
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block", paddingRight: '90px', paddingBottom: '100px'}}
            onClick={onClick}

        />
    );
}

export default function Responsive () {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        responsive: [
            {
                breakpoint: 1280,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 980,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };
    return (
        <Slider {...settings}>
            <div className={s.SimpleSliderContent}>
                <img src={image1} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
            <div className={s.SimpleSliderContent}>
                <img src={image2} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
            <div className={s.SimpleSliderContent}>
                <img src={image3} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
            <div className={s.SimpleSliderContent}>
                <img src={image4} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
            <div className={s.SimpleSliderContent}>
                <img src={image1} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
            <div className={s.SimpleSliderContent}>
                <img src={image2} />
                <h4>ФЕСТИВАЛЬ</h4>
                <h3>ФЕСТИВАЛЬ ДЖАЗОВОЙ <br/>МУЗЫКИ В САМАРА</h3>
            </div>
        </Slider>
    );
}
