import React from 'react';
import s from './Announcements.module.css'
import elowSircle from './AnnouncementsAccets/elowSircle.png'
import greySircle from './AnnouncementsAccets/greySircle.png'

let Announcements = () => {
    return (
        <div className={s.body}>
           <div className={s.bodyContent}>
               <h1>АНОНСЫ</h1>
               <div className={s.mainBlock}>
                   <div className={s.blockOne}>
                       <div className={s.blockOneContainer}>
                           <div className={s.blockOneContainerContent}>
                                          <div className={s.blockOneContainerContentDate}>
                                              <table>
                                                  <tbody>
                                                      <td>
                                                          <h2>14</h2>
                                                      </td>
                                                      <td>
                                                          <h3>МАРТА</h3>
                                                      </td>
                                                      <td>
                                                          <h4>11:00</h4>
                                                      </td>
                                                  </tbody>
                                              </table>
                                          </div>
                               <div className={s.blockOneContainerContentText}>
                                   <h3>ПРИГЛАШАЕМ</h3>
                                   <p>Познакомиться с учебным <br/>процессом, не отходя от <br/>компьютера</p>
                               </div>
                               <div className={s.online}>
                                   <table>
                                       <tr>
                                           <td>
                                               <img src={elowSircle} alt='желтый кружок'/>
                                           </td>
                                           <td>
                                               <p>онлайн</p>
                                           </td>
                                       </tr>
                                   </table>
                           </div>
                           </div>

                       </div>
                   </div>
                   <div className={s.blockTwo}>
                       <div className={s.blockOneContainer}>
                           <div className={s.blockOneContainerContent}>
                               <div className={s.blockTwoContainerContentDate}>
                                   <table>
                                       <tr>
                                           <td>
                                               <h2>25</h2>
                                           </td>
                                           <td>
                                               <h3>АПРЕЛЯ</h3>
                                           </td>
                                           <td>
                                               <h4>11:00</h4>
                                           </td>
                                       </tr>
                                   </table>
                               </div>
                               <div className={s.blockOneContainerContentText}>
                                   <h3>ПРИГЛАШАЕМ</h3>
                                   <p>Своими глазами увидеть <br/>процесс обучения и как <br/>работают наши мастера</p>
                               </div>
                               <div className={s.online}>
                                   <table>
                                       <tr>
                                           <td>
                                               <img src={greySircle} alt='серый кружок'/>
                                           </td>
                                           <td>
                                               <p>в институте</p>
                                           </td>
                                       </tr>
                                   </table>
                               </div>
                           </div>

                       </div>
                   </div>
                   <div className={s.blockThree}>
                       <div className={s.blockOneContainer}>
                           <div className={s.blockOneContainerContent}>
                               <div className={s.blockThreeContainerContentDate}>
                                   <table>
                                       <tr>
                                           <td>
                                               <h2>28</h2>
                                           </td>
                                           <td>
                                               <h3>АПРЕЛЯ</h3>
                                           </td>
                                           <td>
                                               <h4>11:00</h4>
                                           </td>
                                       </tr>
                                   </table>
                               </div>
                               <div className={s.blockThreeContainerContentText}>
                                   <h3>ПРИГЛАШАЕМ</h3>
                                   <p>Стать участником <br/>мастер-классов и <br/>попробовать себя в роли <br/>студента</p>
                               </div>
                               <div className={s.onlineBlockThree}>
                                   <table>
                                       <tr>
                                           <td>
                                               <img src={greySircle} alt='серый кружок'/>
                                           </td>
                                           <td>
                                               <p>в институте</p>
                                           </td>
                                       </tr>
                                   </table>
                               </div>
                           </div>

                       </div>
                   </div>
               </div>
           </div>
        </div>
    )
}

export default Announcements;