import React from 'react';
import s from './Header.module.css'
import logo from './headerAccets/logoWords/Logo.png'
import glass from './headerAccets/glass.png'
import burger from './headerAccets/burger.png'

let Header = () => {
    return (
        <div className={s.mainHeader}>
            <div className={s.headerContent}>


                <img className={s.headerLogo} src={logo} alt='логотип'/>

               <div className={s.headerGlassAndLang}>
                   <img className={s.headerGlassImg} src={glass} alt='очки'/>
                   <div>
                       <p className={s.headerRusEng}>Rus / Eng</p>
                   </div>

               </div>


                <img className={s.headerBurgerMenu} src={burger} alt='бургер'/>


            </div>

        </div>
        )
}

export default Header;