 import React from 'react'
 import s from './MainImg.module.css'
 import mainImg from './MainIMgAccets/MainImg.png'

 let MainImg = () => {

    return (
        <div>
            <img className={s.mainImg} src={mainImg} alt="Главная картинка"/>
        </div>
    )
 }

 export default MainImg;