import React from 'react';
import s from './Slider.module.css'
import SimpleSlider from "./SimpleSlider";

let Slider = () => {
    return (
        <div className={s.body}>
            <div className={s.sliderContainer}>
                <div className={s.sliderContent}>
                    <h2>АФИША</h2>
                    <SimpleSlider />
                </div>
            </div>
        </div>
    )
}

export default Slider;