import React from 'react'
import s from './AskQuestionForm.module.css'


let AskQuestionForm = () => {
    return (
        <div className={s.body}>
            <div className={s.askQuestionFormContainer}>
                <div>
                    <h2>ЗАДАТЬ ВОПРОС</h2>
                    <div className={s.askQuestionFormModal}>
                        <div className={s.askQuestionFormContent}>
                            <div className={s.mainForm}>
                                <form action="#">
                                    <div className={s.mainFormLabel}>
                                        <label  htmlFor="#">Имя:</label>
                                    </div>
                                    <div>
                                        <input className={s.mainFormInput} type="text" />
                                    </div>
                                    <div className={s.mainFormLabel}>
                                        <label  htmlFor="#">E-mail:</label>
                                    </div>
                                    <div>
                                        <input className={s.mainFormInput} type="text"/>
                                    </div>
                                    <textarea placeholder='Ваше сообщение'className={s.mainFormItTextArea}></textarea>
                                    <div className={s.mainFormCheckBoxAndBtn}>
                                        <table>
                                            <tbody>
                                                <td>
                                                    <input type="checkbox" className={s.mainFormCheckBox}id="music" name="interest" value="music"/>
                                                </td>
                                                <td>
                                                    <small>согласие на обработку персональных данных</small>
                                                </td>
                                                <td>
                                                    <button type="button" className={s.mainFormButton}>Отправить</button>
                                                </td>
                                            </tbody>
                                        </table>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AskQuestionForm;